body {
  font-family: 'Roboto';
  font-size: 16px;
  margin: 0;
  padding: 0;
}

a {
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.top-bar {
  background: lightseagreen;
  margin-bottom: 50px;
  padding: 10px 15px;
}

.top-bar::after {
  clear: both;
  content: '';
  display: block;
}

.top-bar-left {
  float: left;
  font-size: 1.5em;
}

.top-bar-right {
  float: right;
}

.top-bar a {
  margin: 0 10px;
}

.top-bar svg {
  margin: 0 5px;
}

.top-bar button {
  background-color: white;
  margin: 0 2px;
}

.top-bar button:hover {
  background-color: #f1f1f1;
}

.container {
  margin: 0 auto;
  text-align: center;
  max-width: 900px;
}

.card-heading {
  padding: 16px;
}

.field-line,
.button-line {
  padding: 20px;
}

.error-message {
  color: tomato;
  padding: 0 16px;
}

.success-message {
  color: green;
  padding: 0 16px;
}

.gamepage .container,
.homepage .container {
  padding: 30px;
  margin: 20px auto;
}

.divider {
  font-size: 25px;
}

.selector-container {
  margin: 30px 0;
  padding: 10px;
}

.selector-item {
  border: 1px solid lightseagreen;
  cursor: pointer;
  max-width: 200px;
  margin: 20px auto;
  padding: 20px;
}

.selector-item:hover {
  background-color: lightseagreen;
}

.selector-item.active {
  background-color: lightseagreen;
}

.weapon {
  margin: 0 20px !important;
}

.gamepage .container > div {
  display: inline-block;
  margin: 0 40px;
}



